:root {
    /**
     *      .
     *
     * NB#1:  `-apple-system`      shorthand  `font`  IE
     *         Times New Roman.  `font-family`  .
     *
     * NB#2:    Windows  XP-8.1      
     * https://support.microsoft.com/ru-ru/help/2970228/update-to-support-the-new-currency-symbol-for-the-russian-ruble-in-win
     *     .      
     *   Roboto Rouble (~3kb gzipped).     
     *  ,    Segoe UI.
     */
    --font: system-ui, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu,
        Cantarell, "Open Sans", "Helvetica Neue", "Roboto Rouble", sans-serif;

    /**
     *   .    
     *        
     * . ,   .
     *
     *       :  -
     *    .
     */
    --font-weight-light: 300;

    /**
     *      .
     */
    --font-weight-normal: 400;

    /**
     *     . ,     
     *    .
     */
    --font-weight-medium: 500;
    --font-weight-bold: 700;

    /**
     *          .
     */
    --font-size-xxs: 11px;

    /**
     *    .    
     * , ,   .
     */
    --font-size-xs: 12px;

    /**
     *   .    .
     */
    --font-size-s: 13px;

    /**
     *   .
     */
    --font-size-m: 16px;

    /**
     *   .
     */
    --font-size-l: 18px;

    /**
     *    .
     */
    --font-size-xl: 22px;

    /**
     *    .    ,
     * ,     .
     */
    --font-size-2xl: 30px;

    /**
     *    .    .
     */
    --font-size-3xl: 40px;

    /**
     *    .     .
     */
    --font-size-4xl: 48px;

    /**
     *  
     */
    --line-height-normal: 1.4;

    /**
     *  
     */
    --line-height-extra-condensed: 1.1;
    --line-height-condensed: 1.2;
    --line-height-semi-condensed: 1.3;

    /**
     *       .
     */
    --letter-spacing-extra: 1px;
}
