:root {
    /**
     *   .
     */
    --color-accent: rgba(240, 50, 38, 1);

    /**
     *      .    
     *   . ,   
     *  .
     */
    --color-accent-translucent: rgba(240, 50, 38, 0.85);

    /**
     *      .    
     *      .
     * ,      .
     */
    --color-accent-heavy-translucent: rgba(240, 50, 38, 0.25);

    /**
     *   `--color-accent`  .   
     *    ,  `--color-accent`.
     * ,  ""   .
     */
    --color-accent-alt: rgba(158, 12, 0, 1);

    /**
     *     .
     */
    --color-accent-content: var(--color-white);

    /**
     *        .
     */
    --color-accent-content-minor: rgba(255, 255, 255, 0.3);

    /**
     *  .
     */
    --color-error: rgba(255, 92, 92, 1);

    /**
     *     .    
     *     . , 
     *    .
     */
    --color-error-translucent: rgba(255, 92, 92, 0.85);

    /**
     *     .
     */
    --color-error-content: rgba(255, 255, 255, 0.9);

    /**
     * Deprecated set
     * TODO
     */
    --color-white-05-deprecated: rgba(255, 255, 255, 0.05);
    --color-white-20-deprecated: rgba(255, 255, 255, 0.2);
    --color-white-70-deprecated: rgba(255, 255, 255, 0.7);
    --color-white-90-deprecated: rgba(255, 255, 255, 0.9);
    --color-white-95-deprecated: rgba(255, 255, 255, 0.95);
    --color-dark-indigo-20-deprecated: rgba(11, 31, 53, 0.2);
    --color-dark-indigo-25-deprecated: rgba(11, 31, 53, 0.25);
    --color-black-70-deprecated: rgba(0, 0, 0, 0.7);
    --color-black-90-deprecated: rgba(0, 0, 0, 0.9);
}
